.search-list {
    background-color: #fff;
    width: 361px;
    max-height: 557.6px;
    overflow-y: auto;
    position: absolute;
    margin-top: 7px;
    border-radius: 8px;
    box-shadow: rgb(0 0 0/ 12%) 0px 2px 12px;
}

.show-result {
    align-items: center;
    display: flex;
    height: 50px;
    padding: 0px 20px;
    font-weight: 600;
}

.display-show {
    display: none;
}